.app__skills {
	flex: 1;
	width: 100%;
	flex-direction: column;
}

.app__skills-container {
	display: flex;
	flex-direction: row;
	width: 80%;
	margin-top: 3rem;

	@media screen and (max-width: 900px) {
		flex-direction: column;
		width: 100%;
	}
}

.app__skills-list {
	flex: 1;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: flex-start;
	margin-right: 5rem;

	@media screen and (max-width: 900px) {
		margin-right: 0;
		justify-content: center;
		align-items: center;
	}
}

.app__skills-item {
	flex-direction: column;
	text-align: center;
	margin: 1rem;
	transition: all 0.3s ease-in-out;

	div {
		width: 90px;
		height: 90px;
		border-radius: 60%;
		background-color: #fff;

		img {
			width: 50%;
			height: 50%;
		}

		&:hover {
			background-color: #f1eaff;
		}

		@media screen and (min-width: 2000px) {
			width: 150px;
			height: 150px;
		}

		@media screen and (max-width: 900px) {
			width: 70px;
			height: 70px;
		}
	}

	p {
		margin-top: 0.5rem;
		font-weight: 500;
	}

	@media screen and (min-width: 2000px) {
		margin: 1rem 2rem;

		p {
			margin-top: 1rem;
		}
	}
}

.app__skills-exp {
	flex: 1;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;

	@media screen and (max-width: 900px) {
		margin-right: 0;
		justify-content: center;
		align-items: center;
		margin-top: 2rem;
	}
}

.app__skills-exp-item {
	padding-top: 1rem;
	width: 100%;
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: flex-start;
	margin: 1rem 0;
}

.app__skills-exp-works {
	flex: 1;

	.app__skills-exp-work {
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		align-items: flex-start;
		margin-bottom: 1rem;

		h4 {
			font-weight: 500;
		}

		p {
			font-weight: 400;
			color: var(--gray-color);
			margin-top: 5px;
		}
	}
}

.app__skills-exp-year {
	margin-right: 3rem;

	p {
		font-weight: 800;
		color: var(--secondary-color);
	}

	@media screen and (max-width: 450px) {
		margin-right: 1rem;
	}
}

.skills-tooltip {
	max-width: 300px !important;
	background-color: #d9f5ff75 !important;
	box-shadow: 0 0 25px rgba(0, 0, 0, 0.1) !important;
	border-radius: 5px !important;
	padding: 1rem !important;
	color: var(--gray-color) !important;
	text-align: center !important;
	line-height: 1.5 !important;
	opacity: 1 !important;
	font-size: 0.8rem;

	@media screen and (min-width: 2000px) {
		font-size: 1rem !important;
		max-width: 500px !important;
		line-height: 2 !important;
	}
}
